
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Masker from 'vux/src/components/masker/index.vue'


export default {
components: {
XButton,
Box,
Masker
},
data() {
return {
content: '',
title: '',
brief: '',
url: '',
data: [],
getDataUrl: '/api/gdekhback/phone/activity_detail.html'
}
},
mounted() {
this.goDetails();
},
methods: {
goDetails() {
this.$http.post(this.getDataUrl, {
id: this.$route.query.id
}).then((res) => {
this.content = res.data.info.content;
this.url = res.data.info.url;
this.data = res.data.info;
this.title = res.data.info.title.split('|')[0];
this.brief = res.data.info.title.split('|')[1];
})
}
}
}



